@import "./src/styles/index";

.header {
    z-index: 20;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 30px;
    font-weight: 600;
    margin: 0;
    box-shadow: -4px 4px 25px -16px rgba(0,0,0,0.20);
}

.h1 {
    margin: 0;
}

.button {
    padding: 9px 25px;
    background-color: $primary-color;
    color: white;
}

.button:hover {
    background-color: transparent;
    color: $primary-color;
}

.buttonLogOut {
    @extend .button;
    padding: 9px 25px;
    background-color: transparent;
    color: $primary-color;
}

.logoDashboard{
    display: flex;
    justify-content: space-between;

}

.logoDashboard a {
    text-decoration: none;
}

.buttonLogOutMobile {
    @extend .button;
    padding: 9px 25px;
    background-color: transparent;
    color: $primary-color;
    display: none;
}


.buttonLogOut:hover {
    background-color: $primary-color;
    color: white;
}

.a {
    text-decoration: none;
    color: $primary-color;
    margin-right: 30px;
}

.a:hover {
    border-bottom: solid $primary-color 1.5px;
}


@media (max-width: 720px) {

    .header {
        display: block;
    }

    nav {
        margin-top: 7px;
    }

    .button {
        padding: 5px 10px;
    }


    .buttonLogOutMobile {
        display: block;
        background-color: transparent;
        color: $primary-color;
        padding: 5px 10px;
    }

    .buttonLogOutMobile a {
        text-decoration: none;
    }

    .buttonLogOutMobile:hover {
        display: block;
        background-color: black;
        color: white;
        padding: 5px 10px;
    }

    .buttonLogOut {
        display: none;
    }

    .a {
        margin-right: 20px;
    }
  }


@media (max-width: 550px) {


    .button {
        padding: 5px 10px;
    }

    // .buttonLogOut {
    //     padding: 5px 10px;
    // }

    .a {
        margin-right: 20px;
    }
  }