@import "./src/styles/index";

.formDiv {
    width: 50%;
    margin-left: $margin;
    margin-right: 70px;
}

.formDivSearch{
    margin-top: 50px;
    width: 100%;
    margin-bottom: 100px;
}

.formDivSearch form {
    width: 100%;
}

.categoryCreateOpportunity {
    margin-right: 7px;
}

.formOpportunity {
    width: 90%;
}

.error {
    color: $error;
}

.p {
    display: block;
    margin-top: 4px;
    margin-bottom: 7px;
    color: $secondary-color-grey;
}

.pDivider {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.formWrapper {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.formWrapperSearch {
    width: 134%;
    display: flex;
    // flex-wrap: wrap;
}

.error {
    border-radius: 7px;
    padding: 15px 30px;
    border: solid $error-border 1px;
    color: $error-border;
    margin-top: 5px;
    margin-bottom: 10px;
}

.formDiv h3 {
    margin-bottom: 30px;
}

.formDiv input,
.formDiv textarea,
.formDiv .select,
.divider .select,
.formDivSearch .select,
.divider input {
    resize: none;
    overflow: auto;
    font-family: $font;
    font-size: $primary-font-size;
    padding: 20px;
    border-radius: 5px;
    border: solid $secondary-color-grey 1px;
    margin-bottom: 10px;
}


.formDiv .select {
    color: $secondary-color-grey;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
}

[type="date"]::-webkit-inner-spin-button {
    display: none;
}

[type="date"]::-webkit-calendar-picker-indicator {
    display: none;
}

input[type=date]::-webkit-datetime-edit {
    color: $secondary-color-grey;
}

.formDiv .select:focus {
    color: $primary-color;
    border: solid $primary-color 1px;
}

.formDiv input:focus,
.formDiv textarea:focus,
.formDiv .select:focus{
    border: solid $primary-color 1px;
}

.formDiv .select:invalid {
    border: solid $error-border 1px;
}


.formDiv input:optional {
    border: solid $secondary-color-grey 1px;
}

.formDiv input:optional:focus {
    border: solid $primary-color 1px;
}

.formDiv input:optional:invalid {
    border: solid $error-border 1px;
}

.formDiv input:invalid,
.formDiv textarea:invalid,
.formDiv .select:invalid {
    border: solid $error-border 1px;
}

.formDiv input:out-of-range {
    border: solid $error-border 1px;
}


.formDiv button:hover,
.formDivSearch .button:hover {
    color: white;
}

.formDiv button,
.formDivSearch .button {
    color: black;
    margin-top: 15px;
    padding: 15px 30px;
}


.divider input:invalid {
    border: solid $error-border 1px;
}

.divider {
    width: 100%;
    display: flex;
    align-items: stretch;
}


.divider input {
    width: 100%;
    margin-right: 7px;
    //fixes overflowing input box in mozilla
    min-width: 0;
}


.divider [type="number"],
.divider .houseNumber {
    width: 30%;
}

.divider .inputText {
    width: 100%;
}

.divider input:last-child {
    margin-right: 0px;
}

.imgContainer {
    align-self: baseline;
}

.divider .numberOfParticipants {
    width: 40%;
}



.formDiv .select option,
.divider .select option {
    color: black;
}


.select {
    width: 100%;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 98%;
    background-position-y: 50%;
}

.formWrapperSearch select {
    width: 30%;
    margin-right: 10px;
}


@media (max-width: 1150px) {
    .page {
        display: block;
    }

    .imgContainer {
        display: none;
    }
    .formWrapper {
        width: 100%;
    }

    .formWrapperSearch {
        display: block;
    }


    .formDiv {
        box-sizing: border-box;
        width: 85%;
        margin-left: $margin-mobile;
        margin-right: 0;
    }
    
}

@media (max-width: 500px) {

    .formWrapperSearch select {
        display: block;
        width: 100%;
    }
}