@import "./src/styles/index";

.profileWrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.a {
    font-weight: 600;
    text-decoration: none;
    color: $primary-color;
    margin-left: 30px;
    border-bottom: solid $primary-color 1.5px;
}

.button {
    padding: 9px 25px;
    background-color: transparent;
    color: $primary-color;
}

.button:hover {
    background-color: $primary-color;
    color: white;
}

.block {
    width: 100%;
    display: block;
}

.description {
    height: auto;
}

.a:hover {
    border-bottom: 0;
}


.profile label {
    display: block;
    margin: 10px 10px 30px 0;
    font-weight: 800;
}

.profile input,
.profile textarea,
.profile .selected {
    width: 100%;
    display: block;
    margin-top: 10px;
    resize: vertical;
    color: $secondary-color-grey;
    font-family: $font;
    font-size: $primary-font-size;
    padding: 20px;
    border-radius: 5px;
    border: solid $secondary-color-grey 1px;
    margin-bottom: 10px;
}


.profile input::placeholder,
.profile textarea::placeholder {
    color: $secondary-color-grey;
  }

  .profile .selected option {
      color: $primary-color;
    }
  
.profile textarea {
    width: 70%;
}

.profile {
    width: 100%;
    // display: flex;
    // align-items: stretch;
}


.profile input,
.profile textarea,
.profile .selected {
    width: 100%;
    margin-right: 7px;
    //fixes overflowing input box in mozilla
    min-width: 0;
}


.profile .inputText {
    width: 100%;
}

.profile input:last-child {
    margin-right: 0px;
}


input:focus,
textarea:focus, 
.selected:focus {
    color: $primary-color;
    border: solid $primary-color 1px;
}

input:invalid,
textarea:invalid,
.selected:invalid {
    color: $primary-color;
    border: solid $error-border 1px;
}

.selected {
    width: 100%;
    color: $primary-color;
    -webkit-appearance: none;
    -moz-appearance: none;
    background: transparent;
    background-image: url("data:image/svg+xml;utf8,<svg fill='black' height='24' viewBox='0 0 24 24' width='24' xmlns='http://www.w3.org/2000/svg'><path d='M7 10l5 5 5-5z'/><path d='M0 0h24v24H0z' fill='none'/></svg>");
    background-repeat: no-repeat;
    background-position-x: 98%;
    background-position-y: 50%;
}


input:disabled,
textarea:disabled, 
.selected:disabled,
.selected[disabled],
.profile:disabled {
    resize: none;
    cursor: text;
    font-family: $font;
    color: $primary-color;
    font-size: $primary-font-size;
    line-height: $line-height;
    background-color: transparent;
    border: none;   
    margin: 0;
    padding: 10px 10px 10px 0;
    -o-appearance: none;
    -ms-appearance: none;
    background-image: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
}

.selected option {
    color: black;
}

@media (max-width: 800px) {

    .profileWrapper {
        display: block;
    }
    
}